import React, { useEffect, useState } from 'react';
import {
  Box,
  Typography,
  Card,
  CardContent,
  Grid,
  Chip,
  IconButton,
  Divider,
  Button,
  Avatar,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableRow,
  Paper
} from '@mui/material';
import {
  ArrowBack,
  DirectionsWalk,
  DirectionsBike,
  Terrain,
  Timer,
  Speed,
  TrendingUp,
  TrendingDown,
  Height,
  LocalFireDepartment,
  DeleteOutline,
  CalendarToday,
  AccessTime,
  Straighten,
  Map as MapIcon
} from '@mui/icons-material';
import { useNavigate, useParams } from 'react-router-dom';
import { useData } from '../context/DataContext';
import { format } from 'date-fns';
import { es } from 'date-fns/locale';
import TrainingMap from '../components/TrainingMap';



const TrainingDetail = () => {
  const navigate = useNavigate();
  const { id } = useParams();
  const { trainings, deleteTraining } = useData();
  const [mapCenter, setMapCenter] = useState([40.416775, -3.703790]);
  const [mapZoom, setMapZoom] = useState(13);
  
  const training = trainings.find(t => t.id === parseInt(id));

  useEffect(() => {
    if (training?.route && training.route.length > 0) {
      // Centrar mapa en el primer punto de la ruta
      setMapCenter([training.route[0].lat, training.route[0].lng]);
      setMapZoom(14);
    }
  }, [training]);

  if (!training) {
    return (
      <Box sx={{ p: 3, textAlign: 'center' }}>
        <Typography>Entrenamiento no encontrado</Typography>
        <Button onClick={() => navigate('/home')} sx={{ mt: 2 }}>
          Volver al inicio
        </Button>
      </Box>
    );
  }

  const getActivityIcon = (type) => {
    switch(type) {
      case 'caminar': return <DirectionsWalk sx={{ color: '#2E7D32' }} />;
      case 'ciclismo': return <DirectionsBike sx={{ color: '#1976D2' }} />;
      case 'mtb': return <Terrain sx={{ color: '#8D6E63' }} />;
      default: return <DirectionsWalk />;
    }
  };

  const getActivityColor = (type) => {
    switch(type) {
      case 'caminar': return '#2E7D32';
      case 'ciclismo': return '#1976D2';
      case 'mtb': return '#8D6E63';
      default: return '#757575';
    }
  };

  const formatTime = (seconds) => {
    if (!seconds || seconds < 0) return '0:00';
    const hrs = Math.floor(seconds / 3600);
    const mins = Math.floor((seconds % 3600) / 60);
    const secs = seconds % 60;
    
    if (hrs > 0) {
      return `${hrs}h ${mins}min`;
    }
    return `${mins}:${secs.toString().padStart(2, '0')}min`;
  };

  const formatPace = (pace) => {
    if (!pace || pace === '0:00') return '';
    return `${pace}/km`;
  };

  const handleDelete = () => {
    if (window.confirm('Eliminar este entrenamiento?')) {
      deleteTraining(training.id);
      navigate('/home');
    }
  };

  // Proveedor de mapa
  const mapProvider = (x, y, z) => {
    return `https://a.basemaps.cartocdn.com/light_all/${z}/${x}/${y}.png`;
  };

  return (
    <Box sx={{ p: 2, pb: 10 }}>
      {/* Header */}
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
        <IconButton onClick={() => navigate('/home')} sx={{ mr: 1 }}>
          <ArrowBack />
        </IconButton>
        <Typography variant="h5" sx={{ fontWeight: 700 }}>
          Detalle del entrenamiento
        </Typography>
      </Box>


     <Card sx={{ mb: 2, borderRadius: 3, overflow: 'hidden' }}>
      <TrainingMap 
        route={training.route || []}
        showStartEnd={true}
        height={250}
      />
    </Card>

      {/* Cabecera de actividad */}
      <Card sx={{ mb: 2 }}>
        <CardContent>
          <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, mb: 2 }}>
            <Avatar sx={{ bgcolor: `${getActivityColor(training.type)}20`, width: 56, height: 56 }}>
              {getActivityIcon(training.type)}
            </Avatar>
            <Box>
              <Typography variant="h5" sx={{ fontWeight: 700, textTransform: 'capitalize' }}>
                {training.type === 'mtb' ? 'MTB' : training.type}
              </Typography>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <CalendarToday sx={{ fontSize: 16, color: 'text.secondary' }} />
                <Typography variant="body2" color="text.secondary">
                  {format(new Date(training.date), "EEEE, d 'de' MMMM yyyy", { locale: es })}
                </Typography>
              </Box>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1, mt: 0.5 }}>
                <AccessTime sx={{ fontSize: 16, color: 'text.secondary' }} />
                <Typography variant="body2" color="text.secondary">
                  {training.startTime} - {training.endTime}
                </Typography>
              </Box>
            </Box>
          </Box>
        </CardContent>
      </Card>

      {/* Mtricas principales - CON 2 DECIMALES */}
      <Grid container spacing={2} sx={{ mb: 2 }}>
        <Grid item xs={6}>
          <Card>
            <CardContent>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                Distancia
              </Typography>
              <Typography variant="h4" sx={{ fontWeight: 700, color: 'primary.main' }}>
                {training.distance < 1 
                  ? `${Math.round(training.distance * 1000)} m`
                  : `${training.distance.toFixed(2)} km`
                }
              </Typography>
              <Typography variant="caption" color="text.secondary">
                {training.distance < 1 ? 'Metros' : 'Kilmetros'}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
        <Grid item xs={6}>
          <Card>
            <CardContent>
              <Typography variant="body2" color="text.secondary" gutterBottom>
                Tiempo total
              </Typography>
              <Typography variant="h4" sx={{ fontWeight: 700 }}>
                {formatTime(training.totalTime)}
              </Typography>
              <Typography variant="caption" color="text.secondary">
                movimiento: {formatTime(training.movingTime)}
              </Typography>
            </CardContent>
          </Card>
        </Grid>
      </Grid>

      {/* Mtricas detalladas */}
      <Card sx={{ mb: 2 }}>
        <CardContent>
          <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 2 }}>
            Estadsticas
          </Typography>
          
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Speed sx={{ color: 'text.secondary' }} />
                <Box>
                  <Typography variant="body2" color="text.secondary">
                    Velocidad promedio
                  </Typography>
                  <Typography variant="body1" sx={{ fontWeight: 600 }}>
                    {training.avgSpeed?.toFixed(1) || 0} km/h
                  </Typography>
                </Box>
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Speed sx={{ color: 'text.secondary' }} />
                <Box>
                  <Typography variant="body2" color="text.secondary">
                    Velocidad mxima
                  </Typography>
                  <Typography variant="body1" sx={{ fontWeight: 600 }}>
                    {training.maxSpeed?.toFixed(1) || 0} km/h
                  </Typography>
                </Box>
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Timer sx={{ color: 'text.secondary' }} />
                <Box>
                  <Typography variant="body2" color="text.secondary">
                    Ritmo promedio
                  </Typography>
                  <Typography variant="body1" sx={{ fontWeight: 600 }}>
                    {formatPace(training.avgPace)}
                  </Typography>
                </Box>
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <LocalFireDepartment sx={{ color: '#FF6B6B' }} />
                <Box>
                  <Typography variant="body2" color="text.secondary">
                    Caloras
                  </Typography>
                  <Typography variant="body1" sx={{ fontWeight: 600 }}>
                    {training.calories || 0} kcal
                  </Typography>
                </Box>
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/* Altitud */}
      <Card sx={{ mb: 2 }}>
        <CardContent>
          <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 2 }}>
            Altitud
          </Typography>
          
          <Grid container spacing={2}>
            <Grid item xs={6}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <Height sx={{ color: 'text.secondary' }} />
                <Box>
                  <Typography variant="body2" color="text.secondary">
                    Inicial / Final
                  </Typography>
                  <Typography variant="body1" sx={{ fontWeight: 600 }}>
                    {training.startAltitude || 0}m / {training.endAltitude || 0}m
                  </Typography>
                </Box>
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <TrendingUp sx={{ color: '#4CAF50' }} />
                <Box>
                  <Typography variant="body2" color="text.secondary">
                    Desnivel +
                  </Typography>
                  <Typography variant="body1" sx={{ fontWeight: 600, color: '#4CAF50' }}>
                    +{training.elevationGain || 0}m
                  </Typography>
                </Box>
              </Box>
            </Grid>
            <Grid item xs={6}>
              <Box sx={{ display: 'flex', alignItems: 'center', gap: 1 }}>
                <TrendingDown sx={{ color: '#F44336' }} />
                <Box>
                  <Typography variant="body2" color="text.secondary">
                    Desnivel -
                  </Typography>
                  <Typography variant="body1" sx={{ fontWeight: 600, color: '#F44336' }}>
                    -{training.elevationLoss || 0}m
                  </Typography>
                </Box>
              </Box>
            </Grid>
          </Grid>
        </CardContent>
      </Card>

      {/* Splits por kilmetro */}
      {training.splits && training.splits.length > 0 && (
        <Card sx={{ mb: 2 }}>
          <CardContent>
            <Typography variant="subtitle1" sx={{ fontWeight: 600, mb: 2 }}>
              Splits por kilmetro
            </Typography>
            
            <TableContainer component={Paper} variant="outlined">
              <Table size="small">
                <TableBody>
                  {training.splits.map((split) => (
                    <TableRow key={split.km}>
                      <TableCell>
                        <Typography sx={{ fontWeight: 600 }}>KM {split.km}</Typography>
                      </TableCell>
                      <TableCell align="right">
                        <Typography variant="body2" color="text.secondary">
                          Ritmo
                        </Typography>
                        <Typography sx={{ fontWeight: 600 }}>{split.pace}/km</Typography>
                      </TableCell>
                      <TableCell align="right">
                        <Typography variant="body2" color="text.secondary">
                          Tiempo
                        </Typography>
                        <Typography sx={{ fontWeight: 600 }}>
                          {Math.floor(split.time / 60)}:{(split.time % 60).toString().padStart(2, '0')}
                        </Typography>
                      </TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </TableContainer>
          </CardContent>
        </Card>
      )}

      {/* Botn eliminar */}
      <Button
        fullWidth
        variant="outlined"
        color="error"
        startIcon={<DeleteOutline />}
        onClick={handleDelete}
        sx={{ mt: 2, py: 1.5 }}
      >
        Eliminar entrenamiento
      </Button>
    </Box>
  );
};

export default TrainingDetail;